<template>
  <div class="box">
    <el-form>

      <el-form-item>
        <div class="collect">
          <div style="color: #606266;">*报名收集：</div>
          <div class="collect-input">
            <ul>
              <li v-for="(item, index) in activityFormList" :key="'activityFormList' + index">
                <el-input size="medium" :placeholder="item.name"></el-input>
              </li>
              <li v-for="(item, index) in userDefinedList" :key="'userDefinedList' + index">
                <el-input size="medium" placeholder="自定义选项">
                  <i slot="suffix" @click="removeList" class="pointer el-input__icon el-icon-remove"></i>
                </el-input>
              </li>

              <el-input size="medium" readonly="readonly" placeholder="自定义选项">
                <i slot="suffix" @click="addList" class="pointer el-input__icon el-icon-circle-plus"></i>
              </el-input>
            </ul>

          </div>
          <div>
            <p v-for="(item, index) in activityFormList" v-show="item.isNeed" :key="index" class="font-small">必填，不可更改</p>
          </div>
        </div>
      </el-form-item>

      <el-form-item label="手机验证：">
        <el-switch active-color="#FC4A46" inactive-color="#E6E6E6"></el-switch>
        <span>手机短信验证开启</span>
      </el-form-item>
    </el-form>

    <div class="next">
      <router-link to="/nav-new/basic-setting/sponsor">
        <img src="static/imgs/manage/editor/next.png">
        <span>下一步</span>
      </router-link>

    </div>
  </div>
</template>

<script type="text/javascript">
export default {
  props: {
    activityFormList: {
      type: Array
    },
    previewType: {
      type: Number
    }
  },
  data () {
    return {
      userDefinedList: []
    }
  },
  methods: {
    addList () {
      this.userDefinedList.push(1)
      this.previewType = 2
    },
    removeList () {
      this.userDefinedList.pop()
      this.previewType = 2
    }
  },
  mounted () {
    console.log(this.activityFormList)
  },
  beforeDestroy () {
    this.previewType = 1
  }
}
</script>
<style type="text/css" src="common/style/module/manage/new-editor.scss" scoped></style>
<style scoped lang="scss">
.collect {
  display: flex;

  .collect-input {
    width: 220px;
    margin: 0 20px;
  }
  div > p {
    margin: 20px 0;
  }
}
.next {
  overflow: hidden;
  zoom: 1;
}
.next > span,
.next > img {
  float: right;
  margin: 0 5px;
}
.next > span {
  position: relative;
  top: 6px;
}
</style>


